.about-wrapper {
  width: 63%;
  line-height: 23px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 35px 0;
}
.section1 {
  background-color: #f8fded;
  min-height: 185px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.section2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #f5f7fb;
}
.about-options {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 63%;
  grid-gap: 8px;
  margin: 15px 0;
}
.option {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  width: 220px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 14px;
  background-color: white;
}
.option:hover {
  cursor: pointer;
  color: green;
}
.h2-title {
  font-family: "Montserrat", "Open Sans", sans-serif;
  margin-top: 10px;
  margin-bottom: 5px;
  font-size: 30px;
  line-height: 30px;
  font-weight: 900;
}
.h3-title {
  font-family: "Montserrat", "Open Sans", sans-serif;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 26px;
  line-height: 30px;
  font-weight: 900;
}
p {
  font-size: 15px;
  margin: 0;
}

@media screen and (max-width: 1024px) {
  .about-wrapper {
    width: 95%;
  }
  .about-options {
    grid-gap: 15px;
    width: 90%;
  }
  .option {
    width: auto;
  }
}
@media screen and (max-width: 725px) {
  .h2-title {
    font-size: 24px;
  }
  .h3-title {
    font-size: 22px;
  }
  p {
    font-size: 14px;
  }
  .about-options {
    grid-template-columns: repeat(2, 1fr);
  }
  .option {
    font-size: 12px;
    margin-bottom: 0;
  }
}
@media screen and (max-width: 325px) {
  .about-options {
    grid-template-columns: 1fr;
  }
}
