@media screen and (max-width: 1281px) {
  .container {
    height: fit-content;
  }
  .wrapper {
    position: relative;
    width: 70%;
    height: fit-content;
  }
  .title {
    margin: 10px 0;
  }
  .game-field {
    width: max-content;
  }
  .game-row {
    margin: 0;
    justify-content: center;
    height: fit-content;
  }
  .letter {
    height: 63px;
    width: 63px;
    margin: 3px;
  }
}
@media screen and (max-width: 1024px) {
  .wrapper {
    position: relative;
    width: 70%;
    height: fit-content;
  }
  .game-field,
  .game {
    height: max-content;
  }
}
@media screen and (max-width: 650px) {
  .logo {
    width: 65%;
  }
  .enter,
  .del {
    width: 50px;
  }
}
@media screen and (max-width: 479px) {
  .wrapper {
    position: relative;
    width: 100%;
    height: fit-content;
    margin-bottom: 60px;
  }

  .game {
    margin: 10px;
  }
  .game-field {
    width: max-content;
  }
  .game-row {
    margin: 0;
    justify-content: center;
    height: fit-content;
  }
  .letter {
    width: 50px;
    height: 50px;
    margin: 3px;
  }
  .key {
    font-size: 14px;
    height: 46px;
  }
  .enter,
  .del {
    font-size: 14px;
    width: 50px;
    height: 46px;
  }
  .lost {
    width: 80%;
  }
}

@media screen and (max-width: 425px) {
  .settings-container {
    margin-bottom: 40px;
  }
  .title {
    margin: 15px 0;
  }
  .game {
    margin: 50px 0;
    height: fit-content;
  }
  .game-field {
    height: fit-content;
  }
  .key {
    font-size: 14px;
    height: 60px;
  }
  .enter,
  .del {
    font-size: 14px;
    width: 50px;
    height: 60px;
  }
}
@media screen and (max-width: 325px) {
  .settings-container {
    justify-content: center;
    margin: 0;
  }
  .setting-right {
    display: none;
  }
  .game-field {
    transform: scale(0.9);
    margin: 0;
  }
  .game {
    margin: 0;
    height: fit-content;
  }
  .key {
    height: 50px;
    font-size: 12px;
  }
  .enter,
  .del {
    height: 50px;
    width: 38px;
    font-size: 12px;
  }
}
