body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
}
a {
  text-decoration: none;
}
button {
  cursor: pointer;
}
.blur {
  opacity: 0.6;
}
.logo {
  width: 38%;
}
.logo:hover {
  opacity: 0.8;
  cursor: pointer;
}
.container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper {
  position: relative;
  width: 40%;
  height: 100vh;
}
.lost {
  width: 60%;
  position: absolute;
  z-index: 10;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: fit-content;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transform: translateY(-25px);
  transition: ease-in 10s;
}
.lost-top {
  color: #69634d;
  background-color: #f3f1ea;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  position: relative;
  border-radius: 10px 10px 0 0;
}
.close {
  position: absolute;
  right: 2%;
  cursor: pointer;
}
.lost-bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px 15px 15px;
}
.lost-bottom p {
  font-size: 14px;
  margin: 10px;
  text-align: center;
}
small {
  margin: 5px 0;
  font-size: 12px;
}
.small1 a {
  color: #3b6fe5;
}
.small1 a:hover {
  border-bottom: 1px solid #3b6fe5;
}
.small2 {
  color: #75819c;
}
.answer {
  background-color: #f1f3f9;
  border: 1px dashed #75819e;
  color: #505050;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.3rem;
}
.restart-button {
  background-color: #79b851;
  padding: 15px 35px;
  font-size: 14px;
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: 700;
}
input {
  outline: none;
  border: none;
  background-color: #f1f3f7;
  width: 100%;
  height: 42px;
  font-size: 15px;
  border-radius: 5px;
  padding-left: 10px;
  margin-bottom: 25px;
}
.custom-button {
  background-color: #4673d9;
  padding: 15px 35px;
  font-size: 14px;
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: 700;
}
.copy-link {
  font-size: 14px;
  padding: 8px 5px;
  background: #f5f7fb;
  margin-top: 5px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #363e52;
  cursor: pointer;
}
.copy-link-icon {
  margin-right: 5px;
}
.title {
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}
.settings-container {
  height: 42px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.setting-left {
  width: fit-content;
  display: flex;
  justify-content: space-around;
}
.language {
  width: 55px;
  background-color: #ebedf3;
  color: #69758e;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.ww {
  width: 40px;
  background-color: #e6f3e6;
  color: #57ac57;
  font-size: 15px !important;
}
.give-up {
  width: 80px;
  background-color: #f3f0e8;
  color: #9a9382;
}
.setting-right {
  width: 130px;
  display: flex;
  justify-content: space-between;
}
.setting-right .settings-button {
  width: 35px;
  cursor: not-allowed;
}
.icon {
  color: #69758e;
  background-color: #ebedf3;
  height: 90%;
}
.flag {
  width: 32%;
  height: 10px;
  margin-right: 2px;
}
.settings-button {
  font-family: "Montserrat", "Open Sans", sans-serif;
  height: 35px;
  border: none;
  font-size: 13px;
  font-weight: 700;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}
.game {
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
}
.game-field {
  min-width: 60%;
  width: fit-content;
  height: fit-content;
  justify-content: space-between;
  margin: 0 15px;
  flex-direction: column;
  position: relative;
}
.game-row {
  margin: 7px;
  flex: 1;
  height: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.letter {
  border: 2px solid #dee1e9;
  background-color: #fbfcff;
  height: 100%;
  margin: 0 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 28px;
  width: 60px;
  height: 60px;
}

.you-lost {
  text-align: center;
}
.you-lost b {
  font-size: 12px;
  padding: 4px 12px;
  border-radius: 20px;
  background: #edf0f7;
  font-weight: 700;
}
.keys {
  height: 140px;
  display: flex;
  flex-direction: column;
  margin: 25px 5px;
  justify-content: space-between;
}
.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
