.second-row .key {
  width: 60px;
}
.third-row .key {
  width: 52px;
} */
.key {
  min-height: 40px;
  color: #5a6376;
  background-color: #dce1ed;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 17px;
  flex: 1;
  margin: 3px;
  cursor: pointer;
}
.letter-absent {
  background-color: #a4aec4;
  color: white;
  border-color: #a4aec4 !important;
}
.letter-elsewhere {
  background-color: #f3c237;
  border-color: #f3c237 !important;
  color: white;
}
.letter-correct {
  background-color: #79b851;
  border-color: #79b851 !important;
  color: white;
}
.border {
  border-color: #a7adc0;
}

.del,
.enter {
  margin: 3px;
  width: 92px;
  height: 40px;
  color: #5a6376;
  background-color: #dce1ed;
  font-weight: 700;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.correct {
  background-color: #79b851;
}

.word-not-found {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 80px;
  width: 300px;
  background-color: white;
  border-radius: 15px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin: auto;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 20px;
  color: #505050;
}
